.categories-main {
  padding: 0 0 30px 0;
  overflow: hidden;
  &__text {
    max-width: 880px;
    margin-top: 24px;
  }
  &__slider {
    margin-top: 56px;
  }
  .swiper {
    overflow: unset;
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(4,calc((100% / 4) - (24px * 3 / 4)));
      grid-auto-rows: 1fr;
      gap: 24px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  &__item {
    display: flex;
    align-items: flex-end;
    border-radius: 20px;
    padding: 115px 16px 16px 16px;
    position: relative;
    overflow: hidden;
    &:nth-child(1) {
      background: rgba(186, 224, 243, 0.70);
    }
    &:nth-child(2) {
      background: rgba(236, 214, 227, 0.70);
    }
    &:nth-child(3) {
      background: rgba(204, 226, 203, 0.70);
    }
    &:nth-child(4) {
      background: rgba(239, 228, 160, 0.70);
    }
    &:nth-child(5) {
      background: rgba(197, 218, 145, 0.70);
    }
    &:nth-child(6) {
      background: rgba(204, 170, 203, 0.70);
    }
    &:nth-child(7) {
      background: rgba(243, 176, 194, 0.70);
    }
    &:nth-child(8) {
      background: rgba(202, 198, 191, 0.70);
    }
    &:nth-child(9) {
      background: rgba(243, 203, 152, 0.50);
    }
    &:nth-child(10) {
      background: rgba(163, 225, 220, 0.70);
    }
    &:nth-child(11) {
      background: rgba(183, 207, 183, 0.70);
    }
    &:nth-child(12) {
      background: rgba(155, 187, 226, 0.70);
    }
    &-img {
      position: absolute;
      top: 0;
      left: 0;
      width: 131px;
      img {
        width: 100%;
      }
    }
  }
}
.other-categories {
  padding: 30px 0 60px;
  &__slider {
    position: relative;
    margin-top: 36px;
  }
  .swiper-button-prev {
    left: -70px;
  }
  .swiper-button-next {
    right: -70px;
  }
  .swiper-pagination {
    display: none;
  }
}


@media screen and (max-width: 1500px) {
  .other-categories {
    &__slider {

    }
    .swiper-button-prev {
      left: -40px;
    }
    .swiper-button-next {
      right: -40px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .categories-main {
    .swiper-wrapper {
      grid-template-columns: repeat(3,calc((100% / 3) - (24px * 2 / 3)));
    }
  }
}

@media screen and (max-width: 992px) {
  .categories-main {
    padding: 30px 0;
    &__text {
      margin-top: 16px;
    }
    &__slider {
      margin-top: 24px;
    }
    .swiper-wrapper {
      align-items: stretch;
    }
    .swiper-slide {
      width: 294px !important;
      height: auto;
    }
  }
  .other-categories {
    padding: 30px 0 90px;
    &__slider {
      margin-top: 24px;
    }
    .swiper-button-prev {
      display: none;
    }
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      bottom: -24px;
      display: block;
    }
  }
}

@media screen and (max-width: 576px) {
  .categories-main {
    .swiper-slide {
      width: 215px !important;
    }
    &__item {
      &-img {
        width: 120px;
      }
    }
  }
}