.account {
  padding-bottom: 60px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__tabs-head {
    display: inline-flex;
    align-items: center;
    border: 1px solid #9D94D7;
    border-radius: 50px;
  }
  &__tabs-body {
    margin-top: 56px;
  }
  &__tab {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 36px;
    color: #9D94D7;
    padding: 0 16px;
    cursor: pointer;
    &--active {
      background: #9D94D7;
      color: #ffffff;
    }
    &:first-child {
      border-radius: 50px 0 0 50px;
    }
    &:last-child {
      border-radius: 0 50px 50px 0;
    }
  }
  .search {
    margin-top: 36px;
  }
  &-cards {
    grid-template-columns: 100%;
    gap: 24px;
    display: none !important;
    &--active {
      display: grid !important;
    }
  }
  &-card {
    display: flex;
    background: #F4F6F7;
    border-radius: 12px;
    padding: 20px 200px 20px 20px;
    position: relative;
    &__status {
      position: absolute;
      top: 20px;
      right: 0;
      width: 180px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50px 0 0 50px;
    }
    .accepted-status {
      background: #EFE4A0;
      span {
        color: #000000;
      }
    }
    .decided-status {
      background: #61D284;
      span {
        color: #1D424B;
      }
    }
    .rejected-status {
      background: #EB6244;
      span {
        color: #ffffff;
      }
    }
    .shipped-status {
      background: #C0C8CD;
      span {
        color: #1D424B;
      }
    }
    &__img {
      flex-shrink: 0;
      width: 157px;
      img {
        width: 100%;
        border-radius: 10px;
      }
    }
    &__desc {
      margin-left: 24px;
    }
    &__top {
      display: flex;
      align-items: center;
    }
    &__title {
      font-weight: 700;
    }
    &__date {
      color: #899DA1;
      margin-left: 24px;
    }
    &__text {
      margin-top: 16px;
    }
    &__address {
      margin-top: 16px;
    }
  }
}

@media screen and (max-width: 992px) {
  .account {
    padding: 30px 0 60px 0;
    &__top {
      display: block;
    }
    &__tabs-head {
      margin: 24px 0 0 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .account {
    &-card {
      padding: 16px 16px 70px 16px;
      &__status {
        top: unset;
        bottom: 16px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .account {
    &__tabs-head {
      max-width: 350px;
      margin: 16px auto 0;
      display: block;
      border: none;
    }
    &__tabs-body {
      margin-top: 48px;
    }
    &__tab {
      border: 1px solid #9D94D7;
      border-radius: 50px !important;
      &:not(:first-child) {
        margin-top: 12px;
      }
    }
    &-cards {
      gap: 16px;
    }
    &-card {
      display: block;
      &__img {
        width: 100%;
        max-width: 300px;
      }
      &__desc {
        margin: 16px 0 0 0;
      }
    }
  }
}