.text14 {
  font-size: 14px;
  line-height: 120%;
  letter-spacing: 0.56px;
}
.text16 {
  font-size: 16px;
  line-height: 120%;
  letter-spacing: 0.64px;
}
.text18 {
  font-size: 18px;
  line-height: 140%;
  letter-spacing: 0.72px;
}
.text20 {
  font-size: 20px;
  line-height: 140%;
  letter-spacing: 0.8px;
}
.text24 {
  font-size: 24px;
  line-height: 140%;
  letter-spacing: 0.96px;
}
.text28 {
  font-family: Mulish;
  font-size: 28px;
  line-height: 120%;
  font-weight: 700;
  letter-spacing: 1.12px;
}
.text32 {
  font-family: Mulish;
  font-size: 32px;
  line-height: 140%;
  font-weight: 700;
  letter-spacing: 3.84px;
}
.text36 {
  font-family: Mulish;
  font-size: 36px;
  line-height: 140%;
  font-weight: 700;
  letter-spacing: 1.44px;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.btn-green {
  background: #61D284;
  span {
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    color: #1D424B;
  }
  &:hover {
    background: #50BA71;
  }
}
.btn-border {
  background: transparent;
  border: 2px solid #1D424B;
  span {
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    color: #1D424B;
    letter-spacing: 0.72px;
    transition: all 0.5s;
  }
  &:hover {
    border: 2px solid rgba(29, 66, 75, 0.50);
    span {
      color: rgba(29, 66, 75, 0.50);
    }
  }
}
.search {
  height: 56px;
  display: flex;
  position: relative;
  input {
    width: 100%;
    height: 100%;
    border: 1px solid #899DA1;
    border-radius: 50px;
    padding: 0 50px 0 22px;
    font-size: 18px;
    line-height: 120%;
    letter-spacing: 0.72px;
    color: #000000;
    &::placeholder {
      color: #899DA1;
    }
  }
  &-btn {
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translate(0, -50%);
    background: transparent;
    padding: 0;
  }
}
.swiper-button-prev {
  z-index: 1;
  width: 30px;
  &:after {
    display: none;
  }
  svg {
    width: 100%;
    height: auto;
  }
}
.swiper-button-next {
  z-index: 1;
  width: 30px;
  &:after {
    display: none;
  }
  svg {
    width: 100%;
    height: auto;
  }
}
.swiper-pagination {
  z-index: 1;
  &-bullet {
    width: 32px;
    height: 6px;
    background: #E2E9ED;
    border-radius: 22px;
    opacity: 1;
  }
  &-bullet-active {
    background: #61D284;
  }
}
.breadcrumbs {
  padding: 24px 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    font-size: 18px;
    line-height: 120%;
    color: #899DA1;
    letter-spacing: 0.72px;
    text-decoration-line: underline;
    margin-bottom: 24px;
    &:hover {
      text-decoration: unset;
    }
    &--active {
      text-decoration: unset;
    }
  }
  &__icon {
    font-size: 18px;
    line-height: 120%;
    color: #899DA1;
    margin: 0 5px;
  }
}
.category-card {
  height: auto;
  display: flex;
  flex-direction: column;
  background: #EDF0F2;
  border-radius: 20px;
  padding: 16px;
  &__icon {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    margin-bottom: auto;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__text {
    margin-top: 34px;
  }
}
.page-top {
  background: #F4F6F7;
  padding: 36px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
    margin-left: 32px;
  }
  &__icon {
    width: 56px;
    margin-right: 24px;
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 28px;
    line-height: 140%;
    font-weight: 700;
    color: #1D424B;
    letter-spacing: 1.12px;
  }
  &__link {
    i {
      display: none;
      width: 21px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 20px;
      line-height: 120%;
      color: #070B0A;
      letter-spacing: 0.8px;

    }
  }
  &__btn {
    width: 306px;
    height: 56px;
    border-radius: 57px;
    margin-left: 24px;
  }
  .nice-select {
    width: 350px;
    height: 56px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #9D94D7 !important;
    border-radius: 50px;
    padding: 5px 10px;
    &:after {
      top: 50%;
      right: 16px;
      transform: translate(0, -50%) rotate(0);
      width: 20px;
      height: 12px;
      background-image: url("../img/arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 20px 12px;
    }
    .current {
      font-size: 20px;
      line-height: 120%;
      font-weight: 400;
      color: #ffffff;
      letter-spacing: 0.8px;
    }
    .list {
      li {
        font-size: 18px;
        line-height: 120%;
        padding: 10px;
      }
    }
  }
  &__menu {
    position: relative;
    z-index: 2;
    display: none;
  }
  &__burger {
    width: 21px;
    height: 13px;
    cursor: pointer;
    position: relative;
    margin-left: 17px;
    margin-right: 15px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 56px;
      height: 56px;
      background: #E2E9ED;
      border-radius: 50%;
    }
    span {
      position: absolute;
      z-index: 2;
      left: 0;
      height: 2px;
      background: #1D424B;
      border-radius: 3px;
      &:nth-child(1) {
        top: 0;
        width: 100%;
      }
      &:nth-child(2) {
        top: 50%;
        left: unset;
        right: 0;
        transform: translate(0, -50%);
        width: 15px;
      }
      &:nth-child(3) {
        bottom: 0;
        width: 100%;
      }
    }
  }
  &__menu-links {
    position: absolute;
    top: calc(100% + 15px);
    right: 0;
    min-width: 250px;
    background: #ffffff;
    box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.15);
    border-radius: 12px;
    padding: 24px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s;
    &--active {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__menu-link {
    display: block;
    &:hover {
      color: #61D284;
    }
    &:not(:first-child) {
      margin-top: 20px;
    }
  }
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  &__item {
    width: 34px;
    height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #C0C8CD;
    &--active {
      background: #9D94D7;
      color: #ffffff;
      border: 1px solid #9D94D7;
    }
  }
  &__btn-prev {
    width: 34px;
    height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #C0C8CD;
    border-radius: 10px 0 0 10px;
  }
  &__btn-next {
    width: 34px;
    height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #C0C8CD;
    border-radius: 0 10px 10px 0;
  }
}
.blue {
  background: #BAE0F3;
}
.pink {
  background: #ECD6E3;
}
.green {
  background: #CCE2CB;
}
.show {
  &--active {
    i {
      transform: rotate(-180deg);
    }
  }
}
.nav {
  padding: 50px 15px;
  .list {
    display: inline-flex;
    flex-direction: column;
    li {
      display: inline-block;
      font-size: 20px;
      line-height: 120%;
      color: #000000;
      transition: all 0.5s;
      &:not(:first-child) {
        margin-top: 15px;
      }
      &:hover {
        opacity: 0.5;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .breadcrumbs {
    display: none;
  }
  .page-top {
    padding: 32px 0;
    &__link {
      flex-shrink: 0;
      width: 56px;
      height: 56px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #E2E9ED;
      border-radius: 50%;
      span {
        display: none;
      }
      i {
        display: block;
      }
      &:hover {
        background: #d2d2d4;
      }
    }
    &__btn {
      width: 280px;
      order: -1;
      margin: 0 20px 0 0;
    }
    &__menu {
      display: block;
    }
  }
}

@media screen and (max-width: 768px) {
  .page-top {
    padding: 24px 0;
    &__inner {
      display: block;
    }
    &__right {
      margin: 24px 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .text18 {
    font-size: 14px;
  }
  .text20 {
    font-size: 16px;
  }
  .text24 {
    font-size: 18px;
  }
  .text28 {
    font-size: 24px;
  }
  .text32 {
    font-size: 24px;
  }
  .text36 {
    font-size: 24px;
  }
  .btn-green {
    span {
      font-size: 16px;
    }
  }
  .swiper-button-prev {
    width: 16px;
  }
  .swiper-button-next {
    width: 16px;
  }
  .category-card {
    &__icon {
      width: 44px;
      height: 44px;
    }
    &__text {
      margin-top: 16px;
    }
  }
  .page-top {
    &__icon {
      width: 46px;
      height: 46px;
    }
    &__icon {
      margin-right: 16px;
    }
    &__btn {
      width: 221px;
      margin-right: 16px;
    }
    &__select {
      width: 100%;
    }
    .nice-select {
      width: 100%;
      .current {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .text28 {
    font-size: 20px;
  }
}
