.statement2 {
  padding: 30px 0 60px;
  &__top {
    display: flex;
    align-items: center;
  }
  &__date {
    min-width: 126px;
    height: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E2E9ED;
    border-radius: 43px;
    padding: 3px;
    color: #899DA1;
  }
  &__address {
    margin-left: 20px;
  }
  &__title {
    margin-top: 24px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 36px;
    margin-top: 48px;
  }
  &__item {
    background: #EDF0F2;
    border-radius: 20px;
    &-show {
      display: flex;
      align-items: center;
      padding: 32px;
      cursor: pointer;
      position: relative;
      &--active {
        i {
          transform: rotate(-180deg);
        }
      }
      span {
        font-weight: 700;
      }
      i {
        flex-shrink: 0;
        width: 30px;
        margin-left: 24px;
        transition: all 0.5s;
        svg {
          width: 100%;
          height: auto;
        }
      }
    }
    &-hidden {
      padding: 0 32px 32px 32px;
    }
    &-rejected {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      height: 40px;
      padding: 0 32px;
      background: #EB6244;
      border-radius: 20px 0 0 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        color: #ffffff;
      }
    }
  }
  &__card {
    max-width: 713px;
    background: #ffffff;
    border-radius: 0 20px 20px 20px;
    padding: 16px;
    position: relative;
    &:not(:first-child) {
      margin-top: 36px;
    }
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: -15px;
      border: 20px solid transparent;
      border-top: 20px solid #ffffff;
      border-radius: 3px 0 0 0;
      pointer-events: none;

    }
    &-top {
      display: flex;
      align-items: center;
      i {
        flex-shrink: 0;
        width: 36px;
        img {
          width: 100%;
        }
      }
      span {
        margin-left: 12px;
      }
    }
    &-img {
      width: 100%;
      border-radius: 10px;
      margin-top: 24px;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-top: 20px;
    }
    &-btns {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
      margin-top: 24px;
    }
    &-btn {
      width: 100%;
      height: 56px;
      background: #F1755A;
      border-radius: 57px;
      &:hover {
        background: #ec5c3b;
      }
      span {
        font-size: 18px;
        line-height: 120%;
        font-weight: 700;
        color: #1D424B;
        letter-spacing: 0.72px;
      }
    }
    &-btn2 {
      width: 100%;
      height: 56px;
      border-radius: 57px;
      span {
        font-weight: 700;
      }
    }
  }
  .bg-violet {
    background: #9D94D7;
    margin-left: auto;
    &:after {
      left: unset;
      right: -20px;
      border-top: 20px solid #9D94D7;
      border-radius: 0 3px 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .statement2 {
    &__top {
      display: block;
    }
    &__date {
      min-width: 101px;
      display: inline-flex;
    }
    &__address {
      margin: 12px 0 0 0;
    }
    &__items {
      gap: 24px;
      margin-top: 32px;
    }
    &__item {
      &-show {
        justify-content: space-between;
        padding: 24px 16px;
        i {
          width: 24px;
        }
      }
      &-show2 {
        padding-bottom: 72px;
      }
      &-hidden {
        padding: 0 16px 24px 16px;
      }
      &-rejected {
        right: unset;
        top: unset;
        left: 0;
        bottom: 24px;
        transform: translate(0);
        border-radius: 0 20px 20px 0;
        font-size: 14px;
        padding: 0 15px;
      }
    }
    &__card {
      &-btns {
        grid-template-columns: 100%;
        gap: 12px;
      }
      &-btn,
      &-btn2 {
        height: 44px;
      }
    }
  }
}