.question-item {
  padding-bottom: 60px;
  &__text {
    max-width: 960px;
    margin-top: 24px;
  }
}
.question {
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 56px;
  }
  &__item {
    background: #ffffff;
    border: 1px solid #C0C8CD;
    border-radius: 20px;
    padding: 24px;
    &-top {
      display: flex;
      justify-content: space-between;
    }
    &-title {
      font-weight: 700;
    }
    &-date {
      color: #899DA1;
      margin-left: 32px;
    }
    &-subtitle {
      color: #899DA1;
      margin-top: 32px;
    }
    &-text {
      margin-top: 16px;
    }
  }
}

@media screen and (max-width: 992px) {
  .question-item {
    padding: 30px 0 60px;
    &__text {
      margin-top: 16px;
    }
  }
  .question {
    &__items {
      margin-top: 36px;
    }
    &__item {
      &-top {
        flex-direction: column;
      }
      &-date {
        order: -1;
        margin: 0 0 16px 0;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .question {
    &__items {
      gap: 16px;
      margin-top: 24px;
    }
    &__item {
      padding: 16px;
    }
  }
}