.supervision {
  padding-bottom: 30px;
  &__title {
    max-width: 960px;
  }
  &__img {
    margin-top: 48px;
    img {
      width: 100%;
    }
  }
  &__desc {
    display: flex;
    justify-content: space-between;
    margin-top: 48px;
    &-left {
      width: calc(100% - 406px);
    }
    &-right {
      width: 306px;
      margin-left: 100px;
    }
  }
  &__text {
    p:not(:first-child) {
      margin-top: 24px;
    }
  }
  &__info {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
  }
  &__info-title {
    color: #899DA1;
  }
  &__info-text {
    margin-top: 16px;
  }
}
.questions2 {
  padding: 30px 0 60px;
  overflow: hidden;
  &__slider {
    position: relative;
    margin-top: 36px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  &-slide {
    height: auto;
    display: flex;
    flex-direction: column;
    border: 1px solid #C0C8CD;
    border-radius: 15px;
    padding: 16px;
    &__icon {
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #E2E9ED;
      border-radius: 50%;
      margin-bottom: auto;
      img {
        width: 12px;
      }
    }
    &__text {
      font-weight: 700;
      margin-top: 36px;
    }
  }
  .swiper-button-prev {
    left: -54px;
  }
  .swiper-button-next {
    right: -54px;
  }
  .swiper-pagination {
    display: none;
    bottom: -24px;
  }
}

@media screen and (max-width: 1500px) {
  .questions2 {
    .swiper-button-prev {
      left: -35px;
    }
    .swiper-button-next {
      right: -35px;
    }
  }
}

@media screen and (max-width: 992px) {
  .supervision {
    padding: 30px 0;
    &__img {
      margin-top: 36px;
    }
    &__desc {
      flex-direction: column;
      margin-top: 36px;
      &-left {
        width: 100%;
      }
      &-right {
        order: -1;
        width: 100%;
        margin: 0 0 36px 0;
      }
    }
    &__info {
      display: flex;
      &-item {
        &:not(:first-child) {
          margin-left: 60px;
        }
      }
    }
  }
  .questions2 {
    &__slider {
      margin-top: 24px;
    }
    .swiper {
      overflow: unset;
    }
    &-slide {
      width: 295px !important;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      display: block;
    }
  }
}

@media screen and (max-width: 576px) {
  .supervision {
    &__img {
      margin-top: 24px;
    }
    &__desc {
      margin-top: 24px;
      &-right {
        margin-bottom: 32px;
      }
    }
    &__info {
      display: block;
      &-item {
        &:not(:first-child) {
          margin: 20px 0 0 0;
        }
      }
    }
  }
  .questions2 {
    &-slide {
      width: 215px !important;
      &__text {
        margin-top: 22px;
      }
    }
  }
}