.search-results {
  padding: 60px 0;
  &__subtitle {
    margin-top: 36px;
    span {
      color: #899DA1;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 56px;
  }
  &__item {
    background: #F4F6F7;
    border-radius: 22px;
    padding: 24px;
    &-text {
      max-width: 960px;
    }
  }
}

@media screen and (max-width: 576px) {
  .search-results {
    padding: 30px 0 60px;
    &__subtitle {
      margin-top: 12px;
    }
    &__items {
      margin-top: 32px;
    }
    &__item {
      padding: 24px 16px;
    }
  }
}