.budget-menu {
  border-bottom: 1px solid #C0C8CD;
  &__list {
    display: flex;
    align-items: center;
  }
  &__link {
    padding: 24px 0;
    &:not(:first-child) {
      margin-left: 32px;
    }
    &--active {
      color: #61D284;
      a {
        text-decoration: underline;
        text-underline-offset: 30px;
      }
    }
    &:hover {
      color: #61D284;
    }
  }
}
.budget-main {
  padding-bottom: 30px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__info {
    display: flex;
    align-items: center;
    margin-left: 32px;
  }
  &__btn {
    width: 196px;
    height: 44px;
    border-radius: 57px;
    margin-left: 24px;
  }
  .nice-select {
    width: 130px;
    height: auto;
    background: transparent;
    border: none;
    padding: 0;
    &:after {
      top: 50%;
      right: 0;
      transform: translate(0, -50%) rotate(0);
      width: 20px;
      height: 12px;
      background-image: url("../img/arrow2.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 20px 12px;
    }
    .current {
      font-family: Mulish;
      font-size: 36px;
      line-height: 120%;
      font-weight: 700;
      color: #61D284;
      letter-spacing: 1.44px;
    }
    .list {
      li {
        font-size: 18px;
        line-height: 120%;
        padding: 10px;
      }
    }
  }
  &__scroll {
    border: 1px solid #C0C8CD;
    border-radius: 20px;
    padding: 24px;
    margin-top: 36px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      height: 6px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #000000;
      border-radius: 3px;
    }
  }
  &__items {
    min-width: 1248px;
    display: grid;
    grid-template-columns: repeat(5, calc(100% / 5 - 160px / 5));
    gap: 40px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 85%;
      height: 8px;
      background: #E2E9ED;
    }
  }
  &__item {
    position: relative;
    z-index: 2;
    text-align: center;
    &-title {
      font-weight: 700;
      color: #899DA1;
    }
    &-number {
      width: 70px;
      height: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #E2E9ED;
      border-radius: 50%;
      margin: 16px auto;
      span {
        font-size: 28px;
        line-height: 120%;
        font-weight: 700;
        color: #899DA1;
        letter-spacing: 1.12px
      }
    }
    &-date {
      color: #899DA1;
    }
    &:last-child {
      .budget-main__item-title {
        color: #000000;
      }
      .budget-main__item-number {
        background: #61D284;
        span {
          color: #1D424B;
        }
      }
      .budget-main__item-date {
        color: #000000;
      }
    }
  }
}
.budget-info {
  padding: 30px 0;
  &__slider {
    background: #F4F6F7;
    border-radius: 40px;
    padding: 90px 36px;
    position: relative;
    margin-top: 36px;
  }
  &__item {
    max-width: 730px;
    margin: 0 auto;
    display: flex;
    align-items: center;
  }
  &__img {
    flex-shrink: 0;
    width: 271px;
    img {
      width: 100%;
    }
  }
  &__desc {
    margin-left: 100px;
  }
  &__subtitle {
    font-family: Mulish;
    font-size: 36px;
    line-height: 140%;
    font-weight: 700;
    letter-spacing: 1.44px;
    span {
      color: #61D284;
    }
  }
  &__text {
    margin-top: 24px;
  }
  .swiper-button-prev {
    left: 50px;
  }
  .swiper-button-next {
    right: 50px;
  }
  .swiper-pagination {
    bottom: 20px;
    &-bullet-active {
      background: #9D94D7;
    }
  }
}
.statistics {
  padding: 30px 0;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(100% / 4 - 72px / 4));
    gap: 24px;
    margin-top: 36px;
  }
  &__card {
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    padding: 20px;
    &-numbers {
      span {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        height: 48px;
        border-radius: 65px;
        padding: 0 16px;
        font-family: Mulish;
        font-size: 32px;
        line-height: 120%;
        font-weight: 700;
        color: #1D424B;
        letter-spacing: 3.84px
      }
    }
    &-icon {
      width: 120px;
      margin: 30px 0 auto auto;
      img {
        width: 100%;
      }
    }
    &-text {
      margin-top: 12px;
    }
    &:first-child {
      grid-column: 1/3;
      background: rgba(172, 222, 231, 0.70);
      .statistics__card-numbers {
        span {
          background: #9BBBE2;
        }
      }
    }
    &:nth-child(2) {
      background: rgba(236, 214, 227, 0.70);
      .statistics__card-numbers {
        span {
          background: #CCAACB;
        }
      }
    }
    &:nth-child(3) {
      background: rgba(204, 226, 203, 0.70);
      .statistics__card-numbers {
        span {
          background: #B7CFB7;
        }
      }
    }
  }
}
.financing {
  padding: 30px 0 60px;
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 36px;
  }
  &__item {
    &-wrap {
      background: #ffffff;
      border: 1px solid #C0C8CD;
      border-radius: 16px;
      padding: 20px;
      &:not(:first-child) {
        margin-top: 24px;
      }
    }
    &-title {
      height: 48px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background: #EDF0F2;
      border-radius: 0 50px 50px 0;
      padding: 0 20px;
      margin-left: -20px;
    }
    &-group {
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
    &-percentages {
      height: 24px;
      display: flex;
      align-items: center;
      margin-top: 16px;
    }
    &-grid {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 50px / 2));
      grid-auto-rows: 1fr;
      gap: 24px 50px;
    }
    &-info {
      display: flex;
      align-items: center;
      span:nth-child(1) {
        flex-shrink: 0;
        width: 32px;
        height: 32px;
        border: 1px solid #899DA1;
        border-radius: 8px;
      }
      span:nth-child(2) {
        margin-left: 16px;
      }
    }
    .big {
      grid-column: 1/3;
    }
  }
  &__percentages {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #1D424B;
    font-weight: 700;
    &:first-child {
      border-radius: 50px 0 0 50px;
    }
    &:last-child {
      border-radius: 0 50px 50px 0;
    }
  }
}

@media screen and (max-width: 1300px) {
  .statistics {
    &__card {
      &-numbers {
        span {
          font-size: 24px;
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .statistics {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
  .financing {
    &__items {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .budget-menu {
    display: none;
  }
  .budget-main {
    padding: 30px 0;
  }
  .budget-info {
    &__slider {
      padding: 80px 16px;
    }
    &__item {
      display: block;
      max-width: 415px;
    }
    &__img {
      width: 240px;
      margin: 0 auto;
    }
    &__desc {
      margin: 80px 0 0 0;
    }
    .swiper-button-prev {
      left: 16px;
    }
    .swiper-button-next {
      right: 16px;
    }
  }
}

@media screen and (max-width: 760px) {
  .budget-main {
    &__top {
      flex-direction: column;
      align-items: start;
    }
    &__info {
      margin: 32px 0 0 0;
    }
  }
  .statistics {
    &__cards {
      grid-template-columns: 100%;
    }
    &__card {
      &-icon {
        margin: 26px auto auto auto;
      }
      &:first-child {
        grid-column: 1;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .budget-main {
    .nice-select {
      width: 96px;
      .current {
        font-size: 24px;
      }
    }
  }
  .budget-info {
    &__slider {
      padding: 60px 12px;
      margin-top: 24px;
    }
    &__img {
      width: 150px;
    }
    &__desc {
      margin-top: 32px;
    }
    &__subtitle {
      font-size: 24px;
    }
    &__text {
      margin-top: 16px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      top: 30%;
    }
  }
  .statistics {
    &__cards {
      margin-top: 24px;
    }
    &__card {
      padding: 16px;
    }
  }
  .financing {
    &__items {
      gap: 16px;
      margin-top: 24px;
    }
    &__item {
      &-wrap {
        padding: 24px 16px;
      }
      &-title {
        margin-left: -16px;
      }
      &-grid {
        grid-template-columns: 100%;
        gap: 16px;
      }
      .big {
        grid-column: unset;
      }
    }
    &__percentages {
      font-size: 12px !important;
    }
  }
}