.mistake {
  padding: 200px 0;
  &__inner {
    text-align: center;
  }
  &__title {
    font-family: Mulish;
    font-size: 128px;
    line-height: 105%;
    font-weight: 700;
    letter-spacing: 5.12px;
  }
  &__subtitle {
    margin-top: 16px;
  }
  &__btn {
    width: 306px;
    height: 56px;
    border-radius: 57px;
    margin: 60px auto 0;
  }
}

@media screen and (max-width: 992px) {
  .mistake {
    padding: 60px 0;
  }
}

@media screen and (max-width: 576px) {
  .mistake {
    &__title {
      font-size: 80px;
    }
    &__btn {
      margin-top: 32px;
    }
  }
}

@media screen and (max-width: 400px) {
  .mistake {
    &__btn {
      width: 100%;
    }
  }
}