.questions {
  padding: 0 0 60px 0;
  &__text {
    margin-top: 24px;
  }
  .search {
    margin-top: 36px;
  }
  &__slider {
    position: relative;
    margin-top: 56px;
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(4,calc((100% / 4) - (24px * 3 / 4)));
      grid-auto-rows: 1fr;
      gap: 24px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
    .swiper-pagination {
      display: none;
    }
  }
  .category-card__text {
    font-weight: 700;
  }
}

@media screen and (max-width: 992px) {
  .questions {
    padding: 30px 0 60px 0;
    &__text {
      margin-top: 16px;
    }
    .search {
      margin-top: 24px;
    }
    &__slider {
      margin-top: 36px;
    }
    .swiper-wrapper {
      align-items: stretch;
    }
    .swiper-pagination {
      bottom: -24px;
      &-bullet {
        width: 44px;
      }
    }
    .category-card__text {
      font-size: 16px;
      font-weight: 400;
    }
  }
}

@media screen and (max-width: 576px) {
  .questions {
    .category-card__text {
      font-size: 12px;
    }
  }
}