.service {
  padding: 0 0 30px 0;
  &__title {
    max-width: 1100px;
  }
  &__text {
    max-width: 880px;
    margin-top: 24px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 56px;
  }
  &__item {
    &-title {
      font-weight: 700;
    }
    &-text {
      margin-top: 14px;
    }
  }
  &__link {
    display: inline-block;
    color: #61D284 !important;
    text-decoration-line: underline;
    margin-top: 36px;
    &:hover {
      text-decoration: unset;
    }
  }
  &__btn {
    width: 307px;
    height: 56px;
    border-radius: 57px;
    margin-top: 56px;
  }
}

@media screen and (max-width: 992px) {
  .service {
    padding: 30px 0;
    &__text {
      margin-top: 16px;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 32px;
      margin-top: 36px;
    }
    &__link {
      margin-top: 32px;
    }
  }
}

@media screen and (max-width: 400px) {
  .service {
    &__btn {
      width: 100%;
      margin-top: 48px;
    }
  }
}