.header {
  padding: 34px 0;
  .container {
    max-width: 1920px;
    padding: 0 16px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    flex-shrink: 0;
    width: 220px;
    img:nth-child(2) {
      display: none;
    }
    img {
      width: 100%;
    }
  }
  .search {
    max-width: 1120px;
    width: 100%;
    margin: 0 32px;
  }
  &__btns {
    flex-shrink: 0;
    display: flex;
    align-items: center;
  }
  .search-mob {
    width: 56px;
    height: 56px;
    background: #E2E9ED;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    display: none;
    svg {
      width: 32px;
      height: auto;
    }
    &:hover {
      background: #d2d2d4;
    }
  }
  &__map {
    width: 56px;
    height: 56px;
    background: #E2E9ED;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
      width: 32px;
      height: auto;
    }
    &:hover {
      background: #d2d2d4;
    }
  }
  &__menu {
    width: 56px;
    height: 56px;
    background: #E2E9ED;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 32px;
    cursor: pointer;
    svg {
      width: 32px;
      height: auto;
    }
    &:hover {
      background: #d2d2d4;
    }
  }
  &__btn {
    width: 264px;
    height: 56px;
    background: #1A90E6;
    border-radius: 57px;
    margin-left: 32px;
    span {
      font-size: 18px;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: 0.72px;
      color: #ffffff;
      margin-left: 8px;
    }
    i {
      flex-shrink: 0;
      width: 22px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &:hover {
      background: #0c73bf;
    }
  }
}
.menu {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: #ffffff;
  padding: 150px 20px 50px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.5s;
  overflow-y: auto;
  &--active {
    opacity: 1;
    pointer-events: all;
  }
  &::-webkit-scrollbar {
    width: 3px;
  }
  &::-webkit-scrollbar-track {
    background: #ffffff;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #000000;
    border-radius: 3px;
  }
  &-close {
    width: 30px;
    height: 20px;
    position: absolute;
    top: 25px;
    right: 25px;
    cursor: pointer;
    span:nth-child(1) {
      position: absolute;
      top: 9px;
      transform: rotate(-45deg);
      width: 100%;
      height: 3px;
      background: #000000;
      border-radius: 3px;
    }
    span:nth-child(2) {
      position: absolute;
      bottom: 8px;
      transform: rotate(45deg);
      width: 100%;
      height: 3px;
      background: #000000;
      border-radius: 3px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
  }
  &__item {
    min-height: 125px;
    border-radius: 20px;
    padding: 25px;
    &:nth-child(1) {
      background: rgba(204, 231, 244, 0.7);;
    }
    &:nth-child(2) {
      background: rgba(239, 224, 233, 0.7);;
    }
    &:nth-child(3) {
      background: rgba(217, 232, 216, 0.7);;
    }
    &:nth-child(4) {
      background: rgba(241, 234, 186, 0.7);;
    }
    &:nth-child(5) {
      background: rgba(212, 227, 175, 0.7);;
    }
    &:nth-child(6) {
      background: rgba(188, 231, 228, 0.7);;
    }
    &:nth-child(7) {
      background: rgba(182, 205, 232, 0.7);;
    }
    &-title {
      font-size: 28px;
      line-height: 120%;
      font-weight: 700;
      color: #1D424B;
    }
    &-list {
      margin-top: 24px;
      li {
        font-size: 20px;
        line-height: 140%;
        color: #232323;
        padding-left: 15px;
        position: relative;
        &:not(:first-child) {
          margin-top: 10px;
        }
        &:after {
          content: '';
          position: absolute;
          top: 10px;
          left: 0;
          width: 5px;
          height: 5px;
          background: #232323;
          border-radius: 50%;
        }
      }
    }
  }
  .big {
    grid-row: 1/3;
  }
}

@media screen and (max-width: 1200px) {
  .header {
    .search {
      display: none;
    }
    .search-mob {
      display: block;
    }
    &__map {
      margin-left: 24px;
    }
    &__menu {
      margin-left: 24px;
    }
    &__btn {
      margin-left: 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 24px 0;
    &__btn {
      width: 56px;
      border-radius: 50%;
      span {
        display: none;
      }
    }
  }
  .menu {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 24px 16px;
    }
    &__item {
      padding: 20px 16px;
      &:last-child {
        grid-column: 1/3;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      width: 55px;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    .search-mob {
      width: 40px;
      height: 40px;
      svg {
        width: 22px;
      }
    }
    &__map {
      width: 40px;
      height: 40px;
      margin-left: 12px;
      svg {
        width: 22px;
      }
    }
    &__menu {
      width: 40px;
      height: 40px;
      margin-left: 12px;
      svg {
        width: 22px;
      }
    }
    &__btn {
      width: 40px;
      height: 40px;
      margin-left: 12px;
      svg {
        width: 22px;
      }
    }
  }
  .menu {
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      &:last-child {
        grid-column: 1;
      }
      &-title {
        font-size: 24px;
      }
      &-list {
        li {
          font-size: 18px;
        }
      }
    }
    .big {
      grid-row: unset;
    }
  }
}