.rules {
  padding: 0 0 60px 0;
  &__title {
    max-width: 1100px;
  }
  &__text {
    max-width: 880px;
    margin-top: 24px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 56px;
  }
  &__item {
    border: 1px solid #C0C8CD;
    border-radius: 20px;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 30px 24px;
      cursor: pointer;
      span {
        color: #070B0A !important;
      }
      i {
        flex-shrink: 0;
        width: 30px;
        margin-left: 24px;
        transition: all 0.5s;
        svg {
          width: 100%;
          height: auto;
        }
      }
    }
    &-hidden {
      border-top: 1px solid #C0C8CD;
      padding: 24px;
    }
    &-img {
      max-width: 800px;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    &-subtitle {
      font-weight: 700;
      margin-bottom: 24px;
    }
    &-list {
      li {
        list-style: auto;
        list-style-type: auto;
        list-style-position: inside;
        &:not(:first-child) {
          margin-top: 24px;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .rules {
    padding: 30px 0 60px;
  }
}

@media screen and (max-width: 576px) {
  .rules {
    &__items {
      gap: 20px;
      margin-top: 32px;
    }
    &__item {
      &-show {
        padding: 16px 12px;
        i {
          width: 24px;
        }
      }
      &-hidden {
        padding: 16px 12px;
      }
    }
  }
}