.statement3 {
  padding-bottom: 140px;
  &__title {
    max-width: 1100px;
  }
  &__text {
    max-width: 960px;
    margin-top: 24px;
    a {
      text-decoration: underline;
      &:hover {
        text-decoration: unset;
      }
    }
  }
  &__btn {
    display: inline-block;
    width: 307px;
    height: 56px;
    background: #E2E9ED;
    border-radius: 57px;
    margin-top: 32px;
    &:hover {
      background: #c0c6d0;
    }
    span {
      font-weight: 700;
      color: #1D424B;
    }
  }
}

@media screen and (max-width: 992px) {
  .statement3 {
    padding: 30px 0 60px;
  }
}

@media screen and (max-width: 400px) {
  .statement3 {
    &__btn {
      width: 100%;
    }
  }
}