.statement {
  padding: 0 0 60px 0;
  &__title {
    max-width: 1100px;
  }
  &__text {
    max-width: 880px;
    margin-top: 24px;
  }
  &-form {
    margin-top: 56px;
    &__title {
      color: #070B0A !important;
    }
  }
  &__check {
    display: inline-flex;
    align-items: center;
    position: relative;
    margin-top: 16px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
      &:checked ~ label:before {
        left: 3px;
      }
    }
    label {
      position: relative;
      padding-left: 44px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 32px;
        height: 16px;
        border: 1px solid #61D284;
        border-radius: 36px;
      }
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 19px;
        transform: translate(0, -50%);
        width: 10px;
        height: 10px;
        background: #61D284;
        border-radius: 50%;
        transition: all 0.5s;
      }
    }
  }
  &__group {
    &:not(:first-child) {
      margin-top: 48px;
    }
  }
  &__inputs {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 16px 24px;
    margin-top: 24px;
    input {
      width: 100%;
      height: 56px;
      font-size: 20px;
      line-height: 120%;
      color: #070B0A;
      letter-spacing: 0.8px;
      border: 1px solid #C0C8CD;
      border-radius: 50px;
      padding: 0 24px;
      &::placeholder {
        opacity: 0.7;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-top: 24px;
  }
  &__btn {
    text-align: center;
    width: 196px;
    height: 36px;
    background: #9D94D7;
    border-radius: 50px;
    padding: 3px;
    span {
      font-size: 20px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
      letter-spacing: 0.8px;
    }
    &:hover {
      background: #7b69dc;
    }
  }
  &__btn2 {
    text-align: center;
    width: 196px;
    height: 36px;
    background: #ffffff;
    border: 1px solid #C0C8CD;
    border-radius: 50px;
    padding: 3px;
    margin-left: 24px;
    span {
      font-size: 20px;
      line-height: 120%;
      font-weight: 700;
      color: #9D94D7;
      letter-spacing: 0.8px;
    }
    &:hover {
      background: #9D94D7;
      span {
        color: #ffffff;
      }
    }
  }
  &__input-address {
    margin-top: 24px;
    input {
      width: 100%;
      height: 56px;
      font-size: 20px;
      line-height: 120%;
      color: #070B0A;
      letter-spacing: 0.8px;
      border: 1px solid #C0C8CD;
      border-radius: 50px;
      padding: 0 24px;
      &::placeholder {
        opacity: 0.7;
      }
    }
  }
  &__textarea {
    margin-top: 24px;
    textarea {
      width: 100%;
      height: 160px;
      font-size: 20px;
      line-height: 120%;
      color: #070B0A;
      letter-spacing: 0.8px;
      border: 1px solid #C0C8CD;
      border-radius: 20px;
      padding: 24px;
      resize: none;
      &::placeholder {
        opacity: 0.7;
      }
    }
  }
  &__file {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    width: 100%;
    height: 165px;
    border: 1px dashed #C0C8CD;
    border-radius: 20px;
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
    }
    .file-text {
      display: flex;
      align-items: center;
      i {
        flex-shrink: 0;
        width: 30px;
        img {
          width: 100%;
        }
      }
      span {
        max-width: 294px;
        font-size: 18px;
        line-height: 140%;
        color: #899DA1;
        letter-spacing: 0.72px;
        margin-left: 12px;
      }
      span:nth-child(3) {
        display: none;
      }
    }
  }
  &__checkboxes {
    display: grid;
    grid-template-columns: 100%;
    gap: 22px;
  }
  &__check2 {
    display: inline-flex;
    align-items: center;
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
      &:checked ~ label {
        &:after {
          background: #61D284;
        }
        &:before {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    label {
      font-size: 20px;
      line-height: 140%;
      color: #070B0A;
      letter-spacing: 0.8px;
      position: relative;
      padding-left: 48px;
      a {
        position: relative;
        z-index: 3;
        text-decoration-line: underline;
        &:hover {
          text-decoration: unset;
        }
      }
      &:after {
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        width: 24px;
        height: 24px;
        border: 1px solid #C0C8CD;
        border-radius: 3px;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 7.5px;
        left: 5px;
        width: 14px;
        height: 12px;
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 14px 12px;
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  &__btn3 {
    width: 294px;
    height: 56px;
    border-radius: 57px;
  }
}

@media screen and (max-width: 992px) {
  .statement {
    padding: 30px 0 60px;
    &__text {
      margin-top: 16px;
    }
    &__check {
      display: none;
    }
    &__inputs {
      grid-template-columns: repeat(2, calc(50% - 16px /2));
      gap: 16px;
    }
    &__file {
      display: inline-flex;
      justify-content: start;
      height: auto;
      border: none;
      border-radius: 0;
      max-width: 170px;
      input {
        right: unset;
      }
      .file-text {
        span:nth-child(2) {
          display: none;
        }
        span:nth-child(3) {
          display: block;
        }
      }
    }
  }
}

@media screen and (max-width: 760px) {
  .statement {
    &__inputs {
      grid-template-columns: 100%;
      gap: 12px;
    }
  }
}

@media screen and (max-width: 576px) {
  .statement {
    &-form {
      margin-top: 48px;
    }
    &__group {
      &:not(:first-child) {
        margin-top: 36px;
      }
    }
    &__inputs {
      margin-top: 16px;
      input {
        font-size: 16px;
        padding: 0 20px;
      }
    }
    &__btns {
      margin-top: 16px;
    }
    &__btn {
      height: 46px;
      span {
        font-size: 16px;
      }
    }
    &__btn2 {
      height: 46px;
      margin-left: 12px;
      span {
        font-size: 16px;
      }
    }
    &__input-address {
      input {
        font-size: 16px;
        padding: 0 20px;
      }
    }
    &__textarea {
      margin-top: 16px;
      textarea {
        font-size: 16px;
        padding: 10px 20px;
      }
    }
    &__file {
      margin-top: 16px;
    }
    &__checkboxes {
      gap: 16px;
    }
    &__check2 {
      label {
        font-size: 16px;
        padding-left: 44px;
        &:after {
          top: 0;
        }
        &:before {
          top: 5.5px;
        }
      }
    }
    &__btn3 {
      width: 100%;
    }
  }
}