.criteria {
  padding: 0 0 60px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__scores {
    height: 68px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: #EDF0F2;
    border-radius: 12px;
    padding: 0 12px;
    margin-left: 32px;
    span:nth-child(2) {
      color: #61D284;
      margin-left: 24px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    border: 1px solid #C0C8CD;
    border-radius: 20px;
    margin-top: 36px;
  }
  &__item {
    &:not(:first-child) {
      .criteria__item-title {
        margin-bottom: 32px;
      }
    }
    &-wrap {
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      padding: 32px 0;
      &:first-child {
        .criteria__item-number {
          align-items: center;
          border-right: 1px solid #C0C8CD;
        }
        .criteria__item-desc {
          display: flex;
          justify-content: center;
          align-items: center;
        }
        .criteria__item-scores {
          align-items: center;
          border-left: 1px solid #C0C8CD;
        }
      }
      &:not(:first-child) {
        border-top: 1px solid #C0C8CD;
        .criteria__item-number {
          &:after {
            content: '';
            position: absolute;
            top: 50%;
            right: 0;
            transform: translate(0, -50%);
            width: 1px;
            height: calc(100% + 64px);
            background: #C0C8CD;
          }
        }
        .criteria__item-scores {
          &:after {
            content: '';
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate(0, -50%);
            width: 1px;
            height: calc(100% + 64px);
            background: #C0C8CD;
          }
        }
      }
    }
    &-number {
      flex-shrink: 0;
      width: 85px;
      display: flex;
      justify-content: center;
      text-align: center;
      padding: 0 5px;
      position: relative;
      span {
        font-weight: 700;
      }
    }
    &-desc {
      padding: 0 24px;
    }
    &-scores {
      flex-shrink: 0;
      width: 170px;
      display: flex;
      justify-content: center;
      text-align: center;
      padding: 0 5px;
      position: relative;
      span {
        font-weight: 700;
      }
    }
    &-title {
      font-weight: 700;
    }
    &-group {
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
    &-subtitle {
      font-weight: 700;
    }
    &-list {
      margin-top: 16px;
      li {
        list-style: inside;
      }
    }
    &-formula {
      display: flex;
      align-items: center;
      margin-top: 16px;
      span:nth-child(1) {
        height: 48px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        font-weight: 700;
        background: #E2E9ED;
        border-radius: 26px;
        padding: 3px 16px;
      }
      span:nth-child(2) {
        margin-left: 8px;
      }
    }
  }
  &__mob {
    display: none;
  }
}

@media screen and (max-width: 1200px) {
  .criteria {
    &__item {
      &-number {
        width: 60px;
      }
      &-scores {
        width: 140px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .criteria {
    padding: 30px 0 60px 0;
    &__top {
      display: block;
    }
    &__scores {
      margin: 24px 0 0 0;
    }
    &__item {
      &-number {
        width: 50px;
      }
      &-scores {
        width: 100px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .criteria {
    &__items {
      margin-top: 32px;
    }
    &__item {
      &:not(:first-child) {
        .criteria__item-title {
          margin-bottom: 20px;
        }
      }
      &:nth-child(1) {
        display: none;
      }
      &:nth-child(2) {
        .criteria__mob {
          border-top: unset !important;
        }
      }
      &-wrap {
        padding: 20px 16px;
      }
      &-number {
        display: none;
      }
      &-desc {
        padding: 0;
      }
      &-scores {
        display: none;
      }
    }
    &__mob {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 16px;
      border-top: 1px solid #C0C8CD;
      &-number {
        display: flex;
        align-items: flex-end;
        span:nth-child(1) {
          font-size: 16px;
          line-height: 120%;
          font-weight: 700;
          letter-spacing: 0.32px;
        }
        span:nth-child(2) {
          font-size: 14px;
          line-height: 120%;
          letter-spacing: 0.32px;
          margin-left: 8px;
        }
      }
      &-scores {
        display: flex;
        align-items: flex-end;
        margin-left: 16px;
        span:nth-child(1) {
          font-size: 16px;
          line-height: 120%;
          font-weight: 700;
          letter-spacing: 0.32px;
        }
        span:nth-child(2) {
          font-size: 14px;
          line-height: 120%;
          letter-spacing: 0.32px;
          margin-left: 8px;
        }
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .criteria {
    &__scores {
      width: 100%;
    }
  }
}