.main {
  padding: 15px 0 30px;
  .container {
    max-width: 1920px;
    padding: 0 16px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__slider {
    width: 662px;
    margin-right: 160px;
    padding-left: 100px;
    &-wrap {
      position: relative;
    }
  }
  &__cards {
    width: calc(100% - 822px);
    display: grid;
    grid-template-columns: repeat(2, calc(50%));
    gap: 0;
  }
  &__card {
    min-height: 420px;
    padding: 32px 12px 32px 32px;
    position: relative;
    overflow: hidden;
    &:nth-child(1) {
      background: #63D285;
      border-radius: 40px 0px 0px 0px;
      .main__card-img {
        position: absolute;
        right: 0;
        bottom: 80px;
        pointer-events: none;
        width: 528px;
        img {
          width: 100%;
        }
      }
    }
    &:nth-child(2) {
      background: #D8F277;
      border-radius: 0px 40px 0px 0px;
      .main__card-img {
        position: absolute;
        right: 0;
        bottom: 0;
        pointer-events: none;
        width: 440px;
        img {
          width: 100%;
        }
      }
    }
    &:nth-child(3) {
      background: #9D94D7;
      border-radius: 0px 0px 0px 40px;
      .main__card-img {
        position: absolute;
        right: 0;
        bottom: 0;
        pointer-events: none;
        width: 303px;
        img {
          width: 100%;
        }
      }
    }
    &:nth-child(4) {
      background: #1D424B;
      border-radius: 0px 0px 40px 0px;
      .main__card-img {
        position: absolute;
        right: 0;
        bottom: 0;
        pointer-events: none;
        width: 442px;
        img {
          width: 100%;
        }
      }
      .main__card-title {
        color: #ffffff;
      }
    }
    &-desc {
      display: flex;
      position: relative;
      z-index: 2;
    }
    &-icon {
      flex-shrink: 0;
      width: 80px;
      height: 80px;
      background: #ffffff;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 56px;
        height: 56px;
      }
    }
    &-title {
      display: flex;
      align-items: center;
      font-size: 28px;
      line-height: 140%;
      font-weight: 700;
      letter-spacing: 1.12px;
      color: #1D424B;
      margin-left: 24px;
    }
    &-img {
      img:nth-child(2) {
        display: none;
      }
    }
  }
  &-slide {
    &__img {
      width: 405px;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    &__title {
      font-family: Mulish;
      font-size: 48px;
      line-height: 140%;
      font-weight: 700;
      letter-spacing: 1.92px;
      margin-top: 60px;
    }
    &__subtitle {
      font-size: 20px;
      line-height: 140%;
      letter-spacing: 0.8px;
      color: #070B0A;
      margin-top: 16px;
    }
    &__btn {
      width: 294px;
      height: 56px;
      border-radius: 57px;
      margin-top: 46px;
    }
  }
  .swiper-button-prev {
    left: -100px;
  }
  .swiper-button-next {
    right: -100px;
  }
  .swiper-pagination {
    bottom: -68px;
  }
}
.news {
  padding: 30px 0 60px;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
    margin-top: 40px;
  }
  &-card {
    display: flex;
    flex-direction: column;
    border: 1px solid #C0C8CD;
    border-radius: 20px;
    &:hover {
      box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
    }
    &__desc {
      height: 100%;
      display: flex;
      flex-direction: column;
      padding: 32px 12px 20px;
    }
    &__img {
      position: relative;
      padding-bottom: 100%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        border-radius: 20px 20px 0 0;
      }
    }
    &__title {
      font-size: 24px;
      line-height: 140%;
      font-weight: 700;
      color: #070B0A;
      letter-spacing: 0.96px;
      margin-bottom: auto;
    }
    &__date {
      font-size: 18px;
      line-height: 120%;
      color: #899DA1;
      letter-spacing: 0.72px;
      margin-top: 27px;
    }
  }
  &__btn {
    width: 196px;
    height: 56px;
    border-radius: 57px;
    margin: 60px auto 0;
  }
}


@media screen and (max-width: 1700px) {
  .main {
    &__slider {
      width: 550px;
      margin-right: 50px;
      padding-left: 50px;
    }
    &__cards {
      width: calc(100% - 600px);
    }
    .swiper-button-prev {
      left: -10px;
    }
    .swiper-button-next {
      right: -10px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .main {
    &__inner {
      display: block;
    }
    &__slider {
      max-width: 600px;
      width: 100%;
      margin: 0 auto 150px auto;
    }
    &__cards {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1200px) {
  .news {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    &__slider {
      max-width: 100%;
      padding-left: 0;
    }
    .swiper-button-prev {
      left: 0;
    }
    .swiper-button-next {
      right: 0;
    }
    &-slide {
      &__title {
        font-size: 36px;
      }
      &__subtitle {
        font-size: 18px;
      }
    }
    &__card {
      &-icon {
        width: 64px;
        height: 64px;
        img {
          width: 44px;
          height: 44px;
        }
      }
      &-title {
        font-size: 24px;
      }
    }
  }
  .news {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 24px 16px;
    }
  }
}

@media screen and (max-width: 760px) {
  .main {
    &-slide {
      &__img {
        width: 100%;
        max-width: 434px;
      }
    }
    &__cards {
      grid-template-columns: 100%;
    }
    &__card {
      min-height: auto;
      padding: 20px;
      &:nth-child(1) {
        border-radius: 40px 40px 0px 0px;
      }
      &:nth-child(2) {
        border-radius: 0;
      }
      &:nth-child(3) {
        border-radius: 0;
      }
      &:nth-child(4) {
        border-radius: 0px 0px 40px 40px;
      }
      &-desc {
        display: block;
      }
      &-title {
        margin: 12px 0 0 0;
      }
      &-img {
        top: 0 !important;
        right: 0 !important;
        left: unset !important;
        bottom: unset !important;
        width: 118px !important;
        img:nth-child(1) {
          display: none;
        }
        img:nth-child(2) {
          display: block;
        }
      }
    }
  }
  .news {
    &__cards {
      margin-top: 16px;
    }
    &-card {
      &__desc {
        padding: 16px 12px;
      }
      &__title {
        font-size: 18px;
      }
      &__date {
        font-size: 16px;
      }
    }
    &__btn {
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding: 50px 0 15px;
    &__slider {
      margin-bottom: 66px;
    }
    &-slide {
      &__img {
        max-width: 288px;
      }
      &__title {
        font-size: 24px;
      }
      &__subtitle {
        font-size: 16px;
        margin-top: 8px;
      }
      &__btn {
        width: 100%;
        margin-top: 24px;
      }
    }
    .swiper-pagination {
      bottom: -24px;
    }
    &__card {
      &-icon {
        width: 40px;
        height: 40px;
        img {
          width: 28px;
          height: 28px;
        }
      }
      &-title {
        font-size: 18px;
      }
    }
  }
  .news {
    padding: 15px 0 60px;
  }
}

@media screen and (max-width: 450px) {
  .news {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 400px) {
  .main {
    &-slide {
      &__img {
        max-width: 209px;
      }
    }
  }
}
