.commission {
  padding-bottom: 60px;
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 36px 24px;
    margin-top: 48px;
  }
  &__card {
    display: flex;
    &-img {
      flex-shrink: 0;
      width: 306px;
      border-radius: 16px;
      img {
        width: 100%;
        border-radius: 16px;
      }
    }
    &-desc {
      margin-left: 24px;
    }
    &-title {
      font-weight: 700;
    }
    &-text {
      margin-top: 16px;
    }
    &-link {
      display: block;
      color: #899DA1;
      margin-top: 10px;
      &:hover {
        color: #070B0A;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .commission {
    &__card {
      &-img {
        width: 200px;
      }
      &-text {
        font-size: 14px !important;
      }
      &-link {
        font-size: 14px !important;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .commission {
    padding: 30px 0 60px 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 24px 16px;
    }
    &__card {
      &-img {
        width: 170px;
      }
      &-desc {
        margin-left: 16px;
      }
      &-text {
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp:5;
        -webkit-box-orient: vertical;
      }
    }
  }
}

@media screen and (max-width: 760px) {
  .commission {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .commission {
    &__cards {
      margin-top: 24px;
    }
    &__card {
      &-img {
        width: 88px;
      }
      &-desc {
        margin-left: 24px;
      }
    }
  }
}