.footer {
  padding: 60px 0;
  border-top: 1px solid #C0C8CD;
  .container {
    max-width: 1582px;
    padding: 0 16px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__text {
    font-size: 20px;
    line-height: 140%;
    font-weight: 700;
    color: #070B0A;
    letter-spacing: 0.4px;
  }
  &__group {
    display: flex;
    align-items: stretch;
    margin-top: 20px;
  }
  &__barcode {
    width: 138px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__downloads {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-left: 16px;
  }
  &__logo {
    display: block;
    width: 264px;
    img {
      width: 100%;
    }
  }
  &__user-agreement {
    display: block;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.8px;
    color: #070B0A;
    text-decoration: underline;
    text-underline-offset: 3px;
    margin-top: 32px;
  }
  &__tel {
    display: block;
    font-size: 24px;
    line-height: 120%;
    color: #070B0A;
    letter-spacing: 0.96px;
    margin-top: 16px;
  }
  &__btn {
    width: 196px;
    height: 56px;
    border-radius: 57px;
    margin-top: 40px;
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding: 40px 0 8px;
    &__inner {
      flex-wrap: wrap;
      align-items: start;
    }
    &__left {
      order: -2;
      margin-right: 32px;
      margin-bottom: 32px;
    }
    &__center {
      margin-bottom: 32px;
    }
    &__right {
      order: -1;
      margin-right: 32px;
      margin-bottom: 32px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__inner {
      display: block;
    }
    &__left {
      margin: 0;
    }
    &__center {
      margin: 32px 0 0 0;
    }
    &__right {
      order: unset;
      margin: 32px 0 0 0;
    }
    &__btn {
      width: 100%;
    }
  }
}