.chat {
  overflow: hidden;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &__left {
    width: calc(100% - 465px);
  }
  &__right {
    width: 465px;
    padding: 32px 24px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 5000px;
      height: 100%;
      background: #F4F6F7;
      box-shadow: -8px 0px 20px 0px rgba(0, 0, 0, 0.10);
    }
  }
  &__info {
    position: relative;
    z-index: 2;
  }
  &__group {
    border: 1px solid #C0C8CD;
    border-radius: 20px;
    padding: 16px 12px;
    margin-top: 24px;
  }
  &__group2 {
    display: grid;
    grid-template-columns: 0.8fr 1.2fr;
    gap: 16px;
    margin-top: 16px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 18px / 3));
    gap: 9px;
    margin-top: 24px;
  }
  &__card {
    border-radius: 16px;
    padding: 12px 8px;
    &-price {
      height: 40px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      border-radius: 65px;
      padding: 3px 10px;
    }
    &-title {
      margin-top: 32px;
    }
  }
  .blue {
    .chat__card-price {
      background: #9BBBE2;
    }
  }
  .pink {
    .chat__card-price {
      background: #CCAACB;
    }
  }
  .green {
    .chat__card-price {
      background: #B7CFB7;
    }
  }
  &__scores {
    background: transparent;
    border: 1px solid #C0C8CD;
    border-radius: 20px;
    padding: 12px 16px;
    &-number {
      margin-top: 24px;
    }
  }
  &__voices {
    background: transparent;
    border: 1px solid #C0C8CD;
    border-radius: 20px;
    padding: 12px 16px;
    &-wrap {
      display: flex;
      align-items: center;
      margin-top: 24px;
    }
    &-vk {
      display: flex;
      align-items: center;
      padding-left: 16px;
      margin-left: 16px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 1px;
        height: 24px;
        background: #C0C8CD;
      }
      i {
        flex-shrink: 0;
        width: 35px;
        margin-left: 8px;
        svg {
          width: 100%;
          height: auto;
        }
      }
      span {
        font-weight: 400;
      }
    }
  }
  &__subtitle {
    font-weight: 700;
    margin-top: 24px;
  }
  &__connection  {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-top: 16px;
    &-item {
      display: flex;
      align-items: center;
      i {
        display: flex;
        align-items: center;
        flex-shrink: 0;
        width: 18px;
      }
      span {
        margin-left: 10px;
      }
    }
  }
  &__menu {
    padding: 24px 0;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: 5000px;
      height: 1px;
      background: #C0C8CD;
    }
  }
  &__list {
    display: flex;
    align-items: center;
  }
  &__link {
    &:not(:first-child) {
      margin-left: 32px;
    }
    &:hover {
      color: #61D284;
    }
    &--active {
      font-weight: 700;
      color: #61D284;
      text-decoration: underline;
      text-underline-offset: 28px;
    }
  }
  &__messages {
    height: 700px;
    padding: 16px;
    overflow-x: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #070b0a;
      border-radius: 3px;
    }
  }
  &__message {
    max-width: 402px;
    background: #F4F6F7;
    border-radius: 20px;
    padding: 16px 16px 32px;
    &:not(:first-child) {
      margin-top: 20px;
    }
    &-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 16px;
      border-bottom: 1px solid #C0C8CD;
    }
    &-name {
      font-weight: 700
    }
    &-date {
      color: #899DA1;
      margin-left: 16px;
    }
    &-desc {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      padding-top: 16px;
    }
    &-file {
      display: flex;
      align-items: center;
      i {
        width: 36px;
        height: 36px;
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #9D94D7;
        border-radius: 50%;
        svg {
          width: 20px;
          height: auto;
        }
      }
      span {
        margin-left: 12px;
      }
    }
    &-img {
      img {
        width: 100%;
        border-radius: 12px;
      }
    }
  }
  .my-message {
    margin-left: auto;
  }
  &__form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    &-file {
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 0;
        margin: 0;
        opacity: 0;
        cursor: pointer;
      }
      label {
        width: 56px;
        height: 56px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #E2E9ED;
        border-radius: 50%;
        svg {
          width: 32px;
          height: auto;
        }
      }
    }
    &-input {
      position: relative;
      flex-grow: 1;
      margin-left: 20px;
      input {
        width: 100%;
        height: 56px;
        background: #ffffff;
        border: 1px solid #899DA1;
        border-radius: 50px;
        padding: 0 60px 0 24px;
        font-size: 18px;
        line-height: 120%;
        color: #899DA1;
      }
    }
    &-btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 21px;
      transform: translate(0, -50%);
      width: 26px;
      background: transparent;
      padding: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__file-group {
    position: relative;
    display: inline-block;
  }
  .list-group {
    display: flex;
    flex-wrap: wrap;
    padding: 0 16px 16px;
    li {
      margin: 0 24px 24px 0;
    }
  }
}

@media screen and (max-width: 1200px) {
  .chat {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      background: #F4F6F7;
      &:after {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .chat {
    &__left {
      width: calc(100% + 30px);
      margin-left: -15px;
    }
    &__right {
      width: calc(100% + 30px);
      margin-left: -15px;
      padding: 32px 16px;
    }
    &__menu {
      padding: 24px 16px;
    }
    &__list {
      overflow-y: auto;
      &::-webkit-scrollbar {
        height: 0;
      }
      &::-webkit-scrollbar-track {
        background: #ffffff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #070b0a;
        border-radius: 3px;
      }
    }
    &__link {
      &:not(:first-child) {
        margin-left: 24px;
      }
    }
    &__message {
      max-width: 280px;
    }
    .my-message {
      background: #ffffff;
      box-shadow: -2px 2px 12px 3px rgba(34, 60, 80, 0.2);
    }
    &__cards {
      grid-template-columns: 100%;
    }
    &__group2 {
      grid-template-columns: 100%;
    }
    &__form {
      position: relative;
      &-file {
        position: absolute;
        z-index: 2;
        top: 50%;
        left: 24px;
        transform: translate(0, -50%);
        label {
          width: 16px;
          height: auto;
          background: transparent;
          svg {
            width: 100%;
            path {
              fill: rgb(192, 200, 205);
              stroke: rgb(192, 200, 205);
            }
          }
        }
      }
      &-input {
        margin-left: 0;
        input {
          height: 46px;
          padding: 0 32px;
        }
      }
      &-btn {
        right: 8px;
        width: 16px;
      }
    }
  }
}