.solving-main {
  padding-bottom: 30px;
  &__subtitle {
    font-size: 20px;
    line-height: 120%;
    color: #070B0A;
    letter-spacing: 0.8px;
    margin-top: 24px;
  }
  .search {
    margin-top: 36px;
  }
  &__slider {
    position: relative;
    margin-top: 56px;
  }
  .swiper {
    margin-top: 36px;
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(6,calc((100% / 6) - (24px * 5 / 6)));
      grid-auto-rows: 1fr;
      gap: 24px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
    .swiper-pagination {
      display: none;
    }
  }
}
.decided {
  padding: 30px 0 60px;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
    margin-top: 36px;
  }
  &-card {
    height: 306px !important;
    border-radius: 20px;
    position: relative;
    overflow: unset;
    &__img {
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        border-radius: 20px;
      }
    }
    .twentytwenty-handle {
      width: 40px;
      height: 40px;
      background: #E2E9ED;
      border: none;
      &:after {
        display: none;
      }
      &:before {
        display: none;
      }
    }
    .twentytwenty-left-arrow {
      border-right: 6px solid #1D424B;
    }
    .twentytwenty-right-arrow {
      border-left: 6px solid #1D424B;
    }
  }
}
.decided2 {
  background: #F4F6F7;
  padding: 56px 0;
  position: relative;
  z-index: 2;
  &__inner {
    overflow-x: auto;
    padding-bottom: 10px;
    &::-webkit-scrollbar {
      height: 6px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #000000;
      border-radius: 3px;
    }
  }
  &__scroll {
    display: flex;
    align-items: stretch;
    width: 100%;
    min-width: 1300px;
  }
  &__statistics {
    flex-shrink: 0;
    width: 306px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-right: 24px;
  }
  &__desc {
    margin-bottom: auto;
  }
  &__btns {
    margin-top: 32px;
  }
  &__numbers {
    font-size: 68px;
    line-height: 120%;
    font-weight: 700;
    color: #61D284;
    letter-spacing: 10.88px;
    margin-top: 24px;
  }
  &__btn {
    width: 100%;
    height: 36px;
    background: #1D424B;
    border-radius: 50px;
    text-align: center;
    span {
      font-size: 20px;
      line-height: 120%;
      color: #ffffff;
      letter-spacing: 0.8px;
    }
    &:hover {
      background: #162c32;
    }
  }
  &__btn2 {
    width: 100%;
    height: 36px;
    border-radius: 50px;
    margin-top: 20px;
    text-align: center;
    span {
      font-size: 20px;
      line-height: 120%;
      color: #ffffff;
      letter-spacing: 0.8px;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
  }
  &-card {
    display: flex;
    flex-direction: column;
    border-radius: 16px;
    padding: 16px;
    &:nth-child(1) {
      background: rgba(186, 224, 243, 0.70);
      .decided2-card__numbers {
        background: #9BBBE2;
      }
    }
    &:nth-child(2) {
      background: rgba(236, 214, 227, 0.70);
      .decided2-card__numbers {
        background: #CCAACB;
      }
    }
    &:nth-child(3) {
      background: rgba(204, 226, 203, 0.70);
      .decided2-card__numbers {
        background: #B7CFB7;
      }
    }
    &__numbers {
      width: 150px;
      height: 48px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 65px;
      margin-bottom: auto;
      span {
        font-family: Mulish;
        font-size: 32px;
        line-height: 105%;
        font-weight: 700;
        color: #1D424B;
        letter-spacing: 3.84px;
      }
    }
    &__text {
      font-size: 20px;
      line-height: 140%;
      font-weight: 700;
      color: #1D424B;
      letter-spacing: 0.8px;
      margin-top: 66px;
      span {
        display: inline-block;
        width: 12px;
        margin-left: 5px;
        position: relative;
        svg {
          position: absolute;
          left: 0;
          bottom: -4px;
          width: 100%;
          height: auto;
        }
      }
    }
  }
}
.advertisement {
  padding: 56px 0;
  &__flex {
    display: flex;
    justify-content: space-between;
    margin-top: 36px;
  }
  &__banner {
    width: 702px;
    margin-right: 68px;
    background: #FFF;
    box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.15);
    border-radius: 2px;
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    width: calc(100% - 770px);
  }
  &__text {
    font-size: 20px;
    line-height: 140%;
    color: #070B0A;
    letter-spacing: 0.8px;
  }
  &__btn {
    width: 196px;
    height: 44px;
    border-radius: 57px;
    margin-top: 40px;
  }
  &__bottom {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 60px;
  }
  &__subtitle {
    font-size: 28px;
    line-height: 120%;
    font-weight: 700;
    color: #1D424B;
    letter-spacing: 1.12px;
  }
  &__socials {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
  }
  &__social {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    &:not(:first-child) {
      margin-left: 24px;
    }
    svg {
      max-width: 100%;
      height: auto;
    }
  }
}


@media screen and (max-width: 1200px) {
  .solving-main {
    .swiper-wrapper {
      grid-template-columns: repeat(5,calc((100% / 5) - (24px * 4 / 5)));
    }
  }
  .decided {
    &-card {
      height: 250px !important;
    }
  }
  .advertisement {
    &__banner {
      width: 500px;
      margin-right: 50px;
    }
    &__desc {
      width: calc(100% - 550px);
    }
  }
}

@media screen and (max-width: 992px) {
  .solving-main {
    padding: 36px 0 50px;
    .breadcrumbs {
      display: none;
    }
    &__slider {
      margin-top: 36px;
    }
    .swiper {
      margin-top: 24px;
    }
    .swiper-wrapper {
      align-items: stretch;
    }
    .swiper-pagination {
      bottom: -24px;
      &-bullet {
        width: 44px;
      }
    }
  }
  .decided {
    padding: 50px 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
      margin-top: 30px;
    }
    &-card {
      height: 280px !important;
    }
  }
  .decided2 {
    padding: 0;
    background: transparent;
    &__scroll {
      min-width: 1240px;
    }
    &__statistics {
      width: 294px;
      margin-right: 16px;
    }
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
      gap: 16px;
    }
  }
  .advertisement {
    &__flex {
      display: block;
      margin-top: 24px;
    }
    &__banner {
      width: 100%;
      margin: 0 0 32px 0;
    }
    &__desc {
      width: 100%;
    }
    &__btn {
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 576px) {
  .solving-main {
    &__subtitle {
      font-size: 18px;
      margin-top: 16px;
    }
    .search {
      margin-top: 24px;
    }
  }
  .decided {
    padding: 30px 0 60px;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 12px / 2));
      gap: 12px;
      margin-top: 24px;
    }
    &-card {
      height: 155px !important;
    }
  }
  .decided2 {
    &__inner {
      overflow: unset;
    }
    &__scroll {
      display: block;
      min-width: unset;
      &-mob {
        overflow-x: auto;
        padding-bottom: 10px;
        &::-webkit-scrollbar {
          height: 6px;
        }
        &::-webkit-scrollbar-track {
          background: #ffffff;
          border-radius: 3px;
        }
        &::-webkit-scrollbar-thumb {
          background: #000000;
          border-radius: 3px;
        }
      }
    }
    &__statistics {
      width: 100%;
      margin: 0 0 20px 0;
    }
    &__cards {
      min-width: 774px;
      width: 100%;
    }
    &__numbers {
      font-size: 56px;
      margin-top: 12px;
    }
    &__btns {
      display: flex;
      justify-content: space-between;
      margin-top: 20px;
    }
    &__btn {
      height: 45px;
      padding: 0 5px;
      span {
        font-size: 14px !important;
      }
    }
    &__btn2 {
      height: 45px;
      padding: 0 5px;
      margin: 0 0 0 12px;
      span {
        font-size: 14px !important;
      }
    }
    &-card {
      &__numbers {
        span {
          font-size: 28px;
        }
      }
      &__text {
        font-size: 16px;
        margin-top: 24px;
        span {
          width: 10px;
          svg {
            bottom: -3px;
          }
        }
      }
    }
  }
  .advertisement {
    &__text {
      font-size: 16px;
    }
    &__btn {
      width: 100%;
    }
    &__bottom {
      margin-top: 50px;
    }
    &__subtitle {
      font-size: 24px;
    }
    &__social {
      width: 28px;
    }
  }
}