.numbers {
  padding: 0 0 60px 0;
  .search {
    margin-top: 36px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
    margin-top: 48px;
  }
  &__card {
    display: flex;
    align-items: center;
    background: #ffffff;
    border: 1px solid #C0C8CD;
    border-radius: 16px;
    padding: 25px 16px;
    &-icon {
      width: 70px;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-left: 24px;
    }
    &-title {
      font-weight: 700;
    }
    &-links {
      margin-top: 16px;
    }
    &-link {
      display: block;
      &:not(:first-child) {
        margin-top: 5px;
      }
    }
  }
  &__btn {
    width: 288px;
    height: 56px;
    background: #E2E9ED;
    border-radius: 57px;
    margin: 32px auto 0;
    display: none !important;
    span {
      color: #1D424B;
    }
    &:hover {
      background: #c0c6d0;
    }
  }
}

@media screen and (max-width: 1200px) {
  .numbers {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
}

@media screen and (max-width: 992px) {
  .numbers {
    padding: 30px 0 60px;
  }
}

@media screen and (max-width: 760px) {
  .numbers {
    &__cards {
      grid-template-columns: 100%;
      gap: 16px;
    }
    &__card {
      &:nth-child(8) ~ .numbers__card {
        display: none;
      }
    }
    &__btn {
      display: flex !important;
    }
  }
}

@media screen and (max-width: 576px) {
  .numbers {
    &__cards {
      margin-top: 32px;
    }
    &__card {
      padding: 16px 12px;
      &-icon {
        width: 50px;
      }
      &-desc {
        margin-left: 20px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .numbers {
    &__btn {
      width: 100%;
    }
  }
}