.project {
  padding-bottom: 60px;
  &__content {
    display: flex;
    justify-content: center;
    margin-top: 56px;
  }
  &__slider {
    width: 624px;
    margin-right: 36px;
  }
  &__desc {
    width: calc(100% - 660px);
  }
  .swiper-slide {
    img {
      width: 100%;
    }
  }
  .swiper-button-prev {
    left: 30px;
  }
  .swiper-button-next {
    right: 30px;
  }
  .swiper-pagination {
    bottom: 24px;
    &-bullet {
      background: #E7ECEF;
    }
    &-bullet-active {
      background: #ffffff;
    }
  }
  &__desc {
    &-top {
      background: #ffffff;
      border: 1px solid #C0C8CD;
      border-radius: 20px;
      padding: 16px;
    }
    &-bottom {
      margin-top: 24px;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 28px / 3));
    gap: 14px;
    margin-top: 16px;
  }
  &__card {
    border-radius: 16px;
    padding: 16px 8px;
    &-price {
      height: 40px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      border-radius: 65px;
      padding: 3px 12px;
      span {
        font-weight: 700;
      }
    }
    &-title {
      margin-top: 36px;
    }
  }
  .blue {
    .project__card-price {
      background: #9BBBE2;
    }
  }
  .pink {
    .project__card-price {
      background: #CCAACB;
    }
  }
  .green {
    .project__card-price {
      background: #B7CFB7;
    }
  }
  &__info {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
  }
  &__scores {
    background: #ffffff;
    border: 1px solid #C0C8CD;
    border-radius: 20px;
    padding: 12px 16px;
    &-number {
      margin-top: 24px;
    }
  }
  &__voices {
    background: #ffffff;
    border: 1px solid #C0C8CD;
    border-radius: 20px;
    padding: 12px 16px;
    &-wrap {
      display: flex;
      align-items: center;
      margin-top: 24px;
    }
    &-vk {
      display: flex;
      align-items: center;
      padding-left: 16px;
      margin-left: 16px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 1px;
        height: 24px;
        background: #C0C8CD;
      }
      i {
        flex-shrink: 0;
        width: 35px;
        margin-left: 8px;
        svg {
          width: 100%;
          height: auto;
        }
      }
      span {
        font-weight: 400;
      }
    }
  }
  &__btn {
    width: 100%;
    height: 56px;
    border-radius: 57px;
    margin-top: 24px;
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    margin-top: 56px;
    &-left {
      width: 624px;
      margin-right: 36px;
    }
    &-right {
      width: calc(100% - 660px);
    }
  }
  &__subtitle {
    font-family: PtAstraSans !important;
  }
  &__text {
    margin-top: 24px;
  }
  &__link {
    display: inline-block;
    color: #9D94D7;
    font-weight: 700;
    margin-top: 12px;
  }
}

@media screen and (max-width: 1350px) {
  .project {
    &__slider {
      width: 500px;
      margin-right: 32px;
    }
    &__desc {
      width: calc(100% - 532px);
    }
    &__bottom {
      &-left {
        width: 500px;
        margin-right: 32px;
      }
      &-right {
        width: calc(100% - 532px);
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .project {
    &__content {
      display: block;
    }
    &__slider {
      width: 100%;
      margin: 0 0 20px 0;
    }
    &__desc {
      width: 100%;
    }
    &__bottom {
      display: block;
      &-left {
        width: 100%;
        margin: 0 0 48px 0;
      }
      &-right {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .project {
    padding: 30px 0 60px;
    &__content {
      margin-top: 32px;
    }
  }
}

@media screen and (max-width: 760px) {
  .project {
    &__desc {
      &-bottom {
        margin-top: 16px;
      }
    }
    &__cards {
      grid-template-columns: 100%;
      gap: 16px;
    }
    &__info {
      grid-template-columns: 100%;
      gap: 16px;
    }
  }
}

@media screen and (max-width: 576px) {
  .project {
    .swiper-button-prev {
      left: 16px;
    }
    .swiper-button-next {
      right: 16px;
    }
    .swiper-pagination {
      bottom: 16px;
    }
    &__bottom {
      margin-top: 36px;
      &-left {
        margin-bottom: 36px;
      }
    }
  }
}