.projects {
  padding: 0 0 30px 0;
  &__grid {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
    margin-top: 36px;
  }
  .search {
    grid-column: 1/4;
  }
  .nice-select {
    width: 100%;
    height: 56px;
    display: flex;
    align-items: center;
    background: #ffffff;
    border: 1px solid #C0C8CD;
    border-radius: 50px;
    padding: 0 24px;
    &:after {
      top: 50%;
      right: 16px;
      transform: translate(0, -50%) rotate(0);
      width: 20px;
      height: 12px;
      background-image: url("../img/arrow2.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 20px 12px;
    }
    .current {
      font-size: 20px;
      line-height: 120%;
      font-weight: 400;
      letter-spacing: 0.8px;
    }
    .list {
      li {
        font-size: 18px;
        line-height: 120%;
        padding: 10px;
      }
    }
  }
  &__radio {
    width: 100%;
    height: 56px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    border: 1px solid #C0C8CD;
    border-radius: 50px;
    padding: 0 24px;
    &-item {
      display: inline-flex;
      align-items: center;
      position: relative;
      &:not(:first-child) {
        margin-left: 24px;
      }
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 0;
        margin: 0;
        opacity: 0;
        cursor: pointer;
        &:checked ~ label {
          &:after {
            border: 1px solid #61D284;
          }
          &:before {
            opacity: 1;
            pointer-events: all;
          }
        }
      }
      label {
        position: relative;
        padding-left: 40px;
        &:after {
          content: '';
          position: absolute;
          top: 3px;
          left: 0;
          width: 20px;
          height: 20px;
          border: 1px solid #C0C8CD;
          border-radius: 50%;
        }
        &:before {
          content: '';
          position: absolute;
          z-index: 1;
          top: 8px;
          left: 5px;
          width: 10px;
          height: 10px;
          background: #61D284;
          border-radius: 50%;
          opacity: 0;
          pointer-events: none;
          transition: all 0.5s;
        }
      }
    }
  }
  .select3 {
    grid-column: 1/4;
  }
  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;
  }
  &__links {
    display: flex;
    align-items: center;
  }
  &__link {
    color: #9D94D7;
    &:not(:first-child) {
      margin-left: 20px;
    }
    &--active {
      color: #070B0A;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-left: 32px;
  }
  &__btn {
    width: 24px;
    &:not(:first-child) {
      margin-left: 24px;
    }
    svg {
      width: 100%;
      height: auto;
    }
  }
}
.filter {
  display: none;
  &-close {
    width: 30px;
    height: 20px;
    position: absolute;
    top: 25px;
    right: 25px;
    cursor: pointer;
    span:nth-child(1) {
      position: absolute;
      top: 9px;
      transform: rotate(-45deg);
      width: 100%;
      height: 3px;
      background: #000;
      border-radius: 3px;
    }
    span:nth-child(2) {
      position: absolute;
      bottom: 8px;
      transform: rotate(45deg);
      width: 100%;
      height: 3px;
      background: #000;
      border-radius: 3px;
    }
  }
  &-btn {
    width: 44px;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E2E9ED;
    border-radius: 50%;
    cursor: pointer;
    svg {
      width: 18px;
      height: auto;
    }
  }
  &-grid {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
  }
}
.objects {
  padding: 30px 0 60px;
  &__top {
    margin-top: 36px;
  }
  &__statistics {
    display: flex;
    align-items: center;
    height: 32px;
    &-item {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      color: #1D424B;
      &:first-child {
        border-radius: 32px 0 0 32px;
      }
      &:last-child {
        border-radius: 0 32px 32px 0;
      }
    }
    &-info {
      display: flex;
      align-items: center;
      &:not(:first-child) {
        margin-left: 24px;
      }
      span:nth-child(1) {
        flex-shrink: 0;
        width: 32px;
        height: 32px;
        border-radius: 50%;
      }
      span:nth-child(2) {
        margin-left: 16px;
      }
    }
  }
  &__info-wrap {
    display: flex;
    align-items: center;
    margin-top: 24px;
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 56px;
  }
  &__pagination {
    margin-top: 56px;
  }
}
.object-card {
  display: flex;
  align-items: stretch;
  background: #ffffff;
  border: 1px solid #C0C8CD;
  border-radius: 12px;
  padding: 20px;
  &__left {
    flex-shrink: 0;
    width: 180px;

  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    margin-left: 24px;
  }
  &__img {
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
  }
  &__desc-left {
    padding-right: 24px;
  }
  &__desc-right {
    flex-shrink: 0;
    width: 260px;
    padding-left: 24px;
    border-left: 1px solid #C0C8CD;
  }
  &__info {
    display: flex;
    align-items: center;
  }
  &__title {
    color: #899DA1;
    margin-left: 20px;
  }
  &__text {
    font-weight: 700;
    max-width: 745px;
    margin-top: 24px;
  }
  &__price {
    margin-top: 16px;
  }
  &__mark {
    text-align: right;
    &-title {
      color: #899DA1;
    }
    &-text {
      font-weight: 700;
      margin-top: 12px;
    }
  }
  &__voices {
    text-align: right;
    margin-top: 32px;
    &-title {
      color: #899DA1;
    }
    &-wrap {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin-top: 12px;
    }
    &-all {
      font-weight: 700;
    }
    &-vk {
      height: 32px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #E2E9ED;
      border-radius: 20px;
      padding: 3px 12px;
      margin-left: 12px;
      i {
        flex-shrink: 0;
        width: 20px;
        margin-left: 8px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .projects {
    &__grid {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 20px 16px;
    }
    .search {
      grid-column: 1/3;
    }
    .select2 {
      grid-column: 1/3;
    }
    .select3 {
      grid-column: 1/3;
    }
  }
  .object-card {
    &__right {
      display: block;
    }
    &__desc-left {
      padding: 0 0 32px 0;
    }
    &__desc-right {
      display: flex;
      width: 100%;
      border-top: 1px solid #C0C8CD;
      border-left: unset;
      padding: 16px 0 0 0;
    }
    &__mark {
      text-align: start;
    }
    &__voices {
      text-align: start;
      margin: 0 0 0 60px;
    }
  }
}

@media screen and (max-width: 992px) {
  .projects {
    padding: 30px 0 0 0;
  }
}

@media screen and (max-width: 760px) {
  .projects {
    &__grid {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .search {
      grid-column: unset;
      flex-grow: 1;
    }
    &__select {
      display: none;
    }
    &__radio {
      display: none;
    }
    .select2,
    .select3 {
      grid-column: unset;
      display: none;
    }
  }
  .filter {
    display: block;
    &-btn {
      margin-left: 24px;
    }
    &-content {
      position: fixed;
      z-index: 5;
      right: -105%;
      top: 0;
      width: 100%;
      height: 100%;
      background: #ffffff;
      transition: all 0.5s;
      padding: 150px 15px 15px;
      overflow-x: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #ffffff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #000000;
        border-radius: 3px;
      }
      &--active {
        right: 0;
      }
    }
    .search,
    .projects__radio,
    .select,
    .select2,
    .select3 {
      display: flex;
    }
  }
  .object-card {
    display: block;
    &__left {
      width: 100%;
      max-width: 260px;
    }
    &__right {
      margin: 16px 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .projects {
    &__info {
      margin-top: 16px;
    }
    &__links {
      flex-direction: column;
      align-items: start;
    }
    &__link {
      &:not(:first-child) {
        margin: 5px 0 0 0;
      }
    }
  }
  .objects {
    &__top {
      margin-top: 24px;
    }
    &__statistics-info {
      &:not(:first-child) {
        margin-left: 20px;
      }
      span:nth-child(1) {
        width: 24px;
        height: 24px;
      }
      span:nth-child(2) {
        font-size: 14px !important;
        margin-left: 12px;
      }
    }
    &__cards {
      margin-top: 36px;
    }
    &__pagination {
      margin-top: 36px;
    }
  }
  .object-card {
    &__left {
      max-width: 100%;
    }
    &__desc-left {
      padding-bottom: 24px;
    }
    &__desc-right {
      padding-top: 24px;
    }
    &__info {
      display: block;
    }
    &__title {
      margin: 8px 0 0 0;
    }
    &__text {
      margin-top: 12px;
    }
    &__price {
      margin-top: 16px;
    }
  }
}